import React from "react";
import styled, { css } from "styled-components";

import { FlexBoxContentCenter } from "../FlexBox";

const StyledHeaderTitle = styled.div`
  ${({ theme }) => {
    return css`
      width: 169px;
      height: 64px;
      background-color: theme.color.ultramarineBlue;

      color: theme.color.white;
      font-size: 24px;
      font-weight: 700;
    `;
  }}
`;

export default function HeaderTitle(): JSX.Element {
  return <StyledHeaderTitle>R.js</StyledHeaderTitle>;
}
